import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  useRouteMatch,
} from "react-router-dom";
import { auth, useSession } from "../../services/firebase";
import { useQuery, gql, useMutation } from "@apollo/client";
import { useHistory } from "react-router";
import {
  SET_SELECTED_PLACE,
  ME,
  ADD_PLACE,
  REMOVE_PLACE,
  ADD_SEED,
  UPDATE_SEED,
} from "../../graphql/mutations";

import Inventory from "./Inventory";
import Saved from "./Saved";
import Overview from "./Overview";

import InputPlace from "../../components/InputPlace";
import InputSeed from "../../components/InputSeed";

import SidebarLayout from "../../layouts/SidebarLayout";

import Header from "../../components/Header";
import * as Our from "../../components/Core";

export default function MyAccount() {
  const { path, url } = useRouteMatch();
  const history = useHistory();
  const userSession = useSession();

  const [showPlaceOptions, setShowPlaceOptions] = useState(false);

  const [darkMode, setDarkMode] = useState(document.body.hasAttribute("mode"));

  const [setSelectedPlace] = useMutation(SET_SELECTED_PLACE);
  const [addPlace] = useMutation(ADD_PLACE);

  const [removePlace] = useMutation(REMOVE_PLACE);
  const [updateSeed] = useMutation(UPDATE_SEED);
  const [addSeed] = useMutation(ADD_SEED);

  const { data: userData, loading: loadingUser, error: userError } = useQuery(
    ME
  );

  function toggleMode(checked) {
    if (!checked) {
      setDarkMode(false);
      document.body.removeAttribute("mode");
    } else {
      document.body.setAttribute("mode", "dark");
      setDarkMode(true);
    }
  }

  function Sidebar() {
    const hasSelectedPlace = userData?.me.selectedPlace;
    const hasAnyPlaces = userData?.me.places.length > 0;

    return (
      <>
        {userData && hasSelectedPlace && hasAnyPlaces && (
          <>
            <Our.Overlay
              open={showPlaceOptions}
              onToggle={(e) => {
                setShowPlaceOptions(e.target.open);
              }}
              style={{ width: "100%" }}
            >
              <Our.Button
                style={{ justifyContent: "space-between" }}
                full
                slot="trigger"
              >
                {userData.me.selectedPlace?.name}
                <i slot="end" className="gg-chevron-down"></i>
              </Our.Button>
              <Our.Box
                bg="white"
                style={{ minWidth: "240px", width: "100%" }}
                depth="sm"
                slot="content"
              >
                <Our.List selectable>
                  {userData.me.places.map((place) => {
                    const selected = userData.me.selectedPlace?.id === place.id;
                    return (
                      <Our.ListItem
                        key={place.id}
                        selected={selected}
                        onClick={() => {
                          setSelectedPlace({
                            variables: {
                              patch: { selectedPlaceId: place.id },
                            },
                            refetchQueries: [{ query: ME }],
                          });
                          setShowPlaceOptions(false);
                        }}
                      >
                        {selected && <i className="gg-check" slot="start"></i>}
                        {place.name}
                      </Our.ListItem>
                    );
                  })}
                </Our.List>
                <hr></hr>
                <Our.List selectable>
                  <Our.ListItem
                    onClick={() => history.push("/dashboard/add-place")}
                  >
                    Add place
                    <i className="gg-math-plus" slot="end"></i>
                  </Our.ListItem>
                </Our.List>
              </Our.Box>
            </Our.Overlay>
            <Our.Box margin-y="md">
              <Our.List selectable>
                <Our.ListItem onClick={() => history.push("/dashboard")}>
                  <i className="gg-template" slot="start"></i>
                  Overview
                </Our.ListItem>
                <Our.ListItem
                  onClick={() => history.push("/dashboard/inventory")}
                >
                  <i className="gg-list" slot="start"></i>
                  Inventory
                </Our.ListItem>
                <Our.ListItem onClick={() => history.push("/dashboard/saved")}>
                  <i className="gg-bookmark" slot="start"></i>
                  Saved
                </Our.ListItem>
                <Our.ListItem
                  onClick={() => {
                    removePlace({
                      variables: {
                        id: userData.me.selectedPlace.id,
                      },
                      refetchQueries: [{ query: ME }],
                    });
                    history.push("/dashboard");
                  }}
                >
                  <i className="gg-trash" slot="start"></i>
                  Remove place
                </Our.ListItem>
              </Our.List>
            </Our.Box>
            <hr></hr>
          </>
        )}
        <Our.Box margin-y="md" padding-x="md">
          <Our.Flex>
            <div>
              <i className="gg-profile"></i>
            </div>
            <Our.Box padding-l="sm">
              <Our.Text color="font-light" size="md">
                {userSession.displayName}
              </Our.Text>
              <Our.Text color="font-light" look="small">
                {userSession.email}
              </Our.Text>
            </Our.Box>
          </Our.Flex>
        </Our.Box>
        <Our.List selectable>
          <Our.ListItem onClick={() => auth.signOut()}>Log out</Our.ListItem>
        </Our.List>
      </>
    );
  }

  const hasPlaces = userData && userData.me.places.length > 0;

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>
      <Our.Box padding-x="lg" padding-y="md">
        <SidebarLayout sidebar={Sidebar} sidebarButtonText="Account">
          <Switch>
            <Route path="/dashboard/add-place">
              <InputPlace
                onSubmit={(input) => {
                  addPlace({
                    variables: { input },
                  }).then((res) => {
                    const newPlaceId = res.data.addPlace.id;
                    setSelectedPlace({
                      variables: {
                        patch: { selectedPlaceId: newPlaceId },
                      },
                      refetchQueries: [{ query: ME }],
                    });
                    history.push("/dashboard");
                  });
                }}
              ></InputPlace>
            </Route>
            {hasPlaces ? (
              <>
                <Route exact path="/dashboard">
                  <Overview></Overview>
                </Route>
                <Route path="/dashboard/inventory">
                  <Inventory></Inventory>
                </Route>
                <Route path="/dashboard/saved" component={Saved}></Route>
                <Route path="/dashboard/add-seed">
                  <InputSeed
                    title="Add Seed"
                    onSubmit={(input) => {
                      addSeed({
                        variables: {
                          input: {
                            ...input,
                            placeId: userData.me.selectedPlace.id,
                          },
                        },
                        refetchQueries: [{ query: ME }],
                      }).then((res) => {
                        history.push("/dashboard");
                      });
                    }}
                  ></InputSeed>
                </Route>
                <Route path="/dashboard/edit-seed">
                  <InputSeed
                    title="Edit Seed"
                    onSubmit={(input) => {
                      updateSeed({
                        variables: {
                          input: {
                            ...input,
                          },
                        },
                        refetchQueries: [{ query: ME }],
                      }).then((res) => {
                        history.push("/dashboard");
                      });
                    }}
                  ></InputSeed>
                </Route>
              </>
            ) : (
              <Route exact path="/dashboard">
                <div>
                  You have no places. Please add a place
                  <Our.Button
                    onClick={() => history.push("/dashboard/add-place")}
                  >
                    Add
                  </Our.Button>
                </div>
              </Route>
            )}
          </Switch>
        </SidebarLayout>
      </Our.Box>
      {userData && !userData.me.selectedPlace && userData.me.places.length > 0 && (
        <Our.Modal open={true}>
          <Our.Box margin-b="lg">
            <Our.Text look="h3">Please select place</Our.Text>
          </Our.Box>
          <Our.List selectable>
            {userData.me.places.map((place) => (
              <Our.ListItem
                onClick={() =>
                  setSelectedPlace({
                    variables: { patch: { selectedPlaceId: place.id } },
                  })
                }
              >
                <Our.Box margin-y="md">
                  <Our.Text no-margin tag="h4">
                    {place.name}
                  </Our.Text>
                  <Our.Text tag="p">{place.address}</Our.Text>
                </Our.Box>
              </Our.ListItem>
            ))}
            <Our.ListItem onClick={() => history.push("/dashboard/add-place")}>
              Add place
              <i className="gg-math-plus" slot="end"></i>
            </Our.ListItem>
          </Our.List>
        </Our.Modal>
      )}
    </>
  );
}
