import React, { useState, useEffect } from "react";
import * as Our from "./Core";
import StepInput from "./StepInput";
import enums from "../constants/enums";

export default function InputSeed({
  title = "Add Seed",
  submitButtonText = "Save",
  resetOnSubmit = false,
  initialState = {
    name: "",
    count: 10,
    price: 0,
    isPublic: true,
    date: "",
    category: undefined,
    sunConditions: [],
    waterConditions: [],
    soilConditions: [],
    temperatureRange: { start: 10, end: 30 },
  },
  onSubmit,
}) {
  const [activeSlide, setActiveSlide] = useState("basic");

  const [isFree, setIsFree] = useState(true);

  const [waterConditionRange, setWaterConditionRange] = useState([
    0,
    enums.waterConditions.length - 1,
  ]);
  const [sunConditionRange, setSunConditionRange] = useState([
    0,
    enums.sunConditions.length - 1,
  ]);
  const [input, setInput] = useState(initialState);

  useEffect(() => {
    setInput(initialState);
  }, [JSON.stringify(initialState)]);

  function handleOnSubmit() {
    const { __typename, id, place, ...data } = input;
    // if user has more places, use the place id from the select input
    // is user only has one place just use the one place
    onSubmit({ ...data });
    if (resetOnSubmit) setInput(initialState);
  }

  return (
    <>
      <Our.Box margin-b="lg">
        <Our.Text tag="h1">{title}</Our.Text>
      </Our.Box>
      <Our.Box margin-y="lg">
        <Our.Label>Name</Our.Label>
        <Our.Input
          value={input.name}
          onInput={(e) => setInput({ ...input, name: e.target.value })}
          full
        >
          <div slot="error">Field is required</div>
        </Our.Input>
      </Our.Box>
      <Our.Box margin-y="lg">
        <Our.Label>Category</Our.Label>
        <Our.Select
          value={input.category}
          onChange={(e) =>
            setInput({
              ...input,
              category: e.target.value,
            })
          }
          full
        >
          {enums.seedCategories.map((cat) => (
            <Our.Option value={cat} key={cat}>
              {cat}
            </Our.Option>
          ))}
        </Our.Select>
      </Our.Box>
      <Our.Box margin-y="lg">
        <Our.Label>Date</Our.Label>
        <Our.Input
          type="date"
          value={input.date}
          onInput={(e) => setInput({ ...input, date: e.target.value })}
          full
        ></Our.Input>
      </Our.Box>

      <Our.Box margin-y="lg">
        <Our.Label>Soil Conditions</Our.Label>
        <Our.Select
          multiple
          value={input.soilConditions}
          onChange={(e) =>
            setInput({
              ...input,
              soilConditions: e.target.value.filter((v) => v !== ""),
            })
          }
          full
        >
          {enums.soilConditions.map((soil) => (
            <Our.Option value={soil} key={soil}>
              {soil}
            </Our.Option>
          ))}
        </Our.Select>
      </Our.Box>
      <Our.Box margin-y="lg">
        <Our.Label>Sun conditions</Our.Label>
        <Our.Range
          sensitivity="0.7"
          labels="Dense shade, Full sun"
          value={sunConditionRange}
          max={enums.sunConditions.length - 1}
          min="0"
          step="1"
          onChange={(e) => {
            const [start, end] = e.target.value;
            const conditions = enums.sunConditions.filter((c, i) => {
              return i >= start && i <= end;
            });
            setInput({
              ...input,
              sunConditions: conditions,
            });
            setSunConditionRange([start, end]);
          }}
        ></Our.Range>
      </Our.Box>
      <Our.Box margin-y="lg">
        <Our.Label>Water conditions</Our.Label>
        <Our.Range
          sensitivity="0.7"
          labels="Dense shade, Full sun"
          value={waterConditionRange}
          max={enums.waterConditions.length - 1}
          min="0"
          step="1"
          onChange={(e) => {
            const [start, end] = e.target.value;
            const conditions = enums.waterConditions.filter((c, i) => {
              return i >= start && i <= end;
            });
            setInput({
              ...input,
              waterConditions: conditions,
            });
            setWaterConditionRange([start, end]);
          }}
        ></Our.Range>
      </Our.Box>

      <Our.Box margin-y="lg">
        <Our.Label>Count</Our.Label>
        <StepInput
          value={input.count}
          onChange={(val) => setInput({ ...input, count: val })}
        ></StepInput>
      </Our.Box>
      <Our.Box margin-y="lg">
        <Our.Toggle
          checked={input.isPublic}
          onChange={(e) => setInput({ ...input, isPublic: e.target.checked })}
        >
          Public
        </Our.Toggle>
      </Our.Box>
      <Our.Box>
        <Our.Toggle
          checked={isFree}
          onChange={(e) => setIsFree(e.target.checked)}
        >
          Free
        </Our.Toggle>
      </Our.Box>
      {!isFree && (
        <Our.Box margin-y="lg">
          <Our.Label>Price</Our.Label>
          <Our.Input
            type="number"
            full
            inputmode="numeric"
            value={input.price}
            onChange={(e) => setInput({ ...input, price: e.target.value })}
          >
            <span slot="end">USD</span>
          </Our.Input>
        </Our.Box>
      )}
      <Our.Box margin-y="lg">
        <Our.Button full type="primary" onClick={() => handleOnSubmit()}>
          {submitButtonText}
        </Our.Button>
      </Our.Box>
    </>
  );
}
