import React from "react";
import * as Our from "../../components/Core";
import { useQuery, useMutation } from "@apollo/client";
import Spinner from "../../components/Spinner";
import { ME, REMOVE_SEED } from "../../graphql/mutations";
import { useHistory } from "react-router";

export default function MyAccount() {
  const history = useHistory();

  const [removeSeed] = useMutation(REMOVE_SEED);

  const {
    data: userData,
    loading: loadingUserData,
    error: userError,
  } = useQuery(ME);

  return (
    <>
      <Our.Text weight="500" tag="h1">
        Inventory
      </Our.Text>

      <Our.Box margin-y="lg">
        {userError && (
          <Our.Box margin-y="md">
            <Our.Text color="danger">{userError.message}</Our.Text>
          </Our.Box>
        )}
        {loadingUserData && (
          <Our.Flex justify-content="center" align-items="center">
            <Spinner></Spinner>
          </Our.Flex>
        )}
        {userData && (
          <Our.Box>
            <Our.List size="xl" bordered>
              {userData.me.selectedPlace?.seeds.map((seed) => {
                return (
                  <Our.ListItem key={seed.id}>
                    <div>
                      <Our.Text full tag="h2" look="p">
                        {seed.name}
                      </Our.Text>
                    </div>

                    <Our.Button
                      slot="end"
                      size="sm"
                      type="transparent"
                      onClick={() => {
                        history.push("/dashboard/edit-seed");
                      }}
                    >
                      Edit
                    </Our.Button>
                    <Our.Button
                      slot="end"
                      size="sm"
                      type="transparent"
                      onClick={() => {
                        removeSeed({
                          variables: { id: seed.id },
                          refetchQueries: [{ query: ME }],
                        });
                      }}
                    >
                      Delete
                    </Our.Button>
                  </Our.ListItem>
                );
              })}
            </Our.List>
          </Our.Box>
        )}
        <Our.Box margin-y="lg">
          <Our.Button
            type="primary"
            onClick={() => {
              history.push("/dashboard/add-seed");
            }}
          >
            <i slot="start" className="gg-math-plus"></i>
            Add seed
          </Our.Button>
        </Our.Box>
      </Our.Box>
    </>
  );
}
